@import url('https://fonts.googleapis.com/css2?family=Jockey+One&family=Source+Code+Pro&family=Work+Sans:wght@300;400&display=swap');

body {
  margin: 0;
  font-family: 'Jockey One', 'Work Sans', 'Source Code Pro',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
